body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-alice: Alice;
  --font-arbutus-slab: "Arbutus Slab";
  --font-kaushan-script: "Kaushan Script";

  /* font sizes */
  --font-size-31xl: 50px;
  --font-size-15xl: 34px;
  --font-size-11xl: 30px;
  --font-size-21xl: 40px;
  --font-size-6xl: 25px;
  --font-size-xl: 20px;
  --font-size-16xl: 35px;

  /* Colors */
  --color-pink: #e4abc9;
  --color-white: #fff;
  --color-thistle: #e6b1cc;
  --color-lightsteelblue: #9ca0c9;

  /* Border radiuses */
  --br-8xs: 5px;
  --br-xs: 12px;
}
